

import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import doctorOrOrg from '../../components/DoctorOrOrg';
import tools from '../../lib/tools.js';
export default {
name:'orgIndex',
components:{Flexbox, FlexboxItem,XButton,Toast,Tab, TabItem,doctorOrOrg},
data(){
return {
id:'',
toastMsg:'',
showToast:false,
info:{},
serviceArr:[],
doctorArr:[],
showTabItem:true
}
},
created(){
this.loading();
this.loadService();
this.loadDoctor();

},
methods:{
loading(){
var vm = this;
let id = tools.getRequestParam('md');
if(!id){return false;}
this.id = id;
this.$http.post('/backstage/queryOne?managerId='+id,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data){
vm.info = data.data;
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
loadService(){
var vm = this;
let id = tools.getRequestParam('md');
if(!id){return false;}

this.$http.post('/service/query?id='+id+'&status=2&pageNumber=1&pageSize=1000&serviceAttribute=1',{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.items){
vm.serviceArr = data.items;
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
goTo(id){
this.serviceArr.some((item) => {
if(item.id == id){
tools.next("/m/serviceIndex.html?serviceId="+id);
}
})
},
wxConfig(){
let _this = this;
let config = {
jsList:['onMenuShareTimeline','onMenuShareAppMessage'],
url:location.href.split('#')[0]
}
let url = '';
let id = localStorage.getItem('md') || tools.getCookie('user_id');
url = id?'&sourceUserId='+id:'';
tools.wxConfig(this.$http,config,(result) => {
wx.ready(function() {
let shareData = {
title: _this.info.name,
desc: _this.info.type,
link: result.link+ 'm/sharing.html' + '?type=3&id='+_this.id+url,
imgUrl: result.link+ 'm/static/img/20170817165354.jpg',
};
console.log('shareData==',shareData);
wx.onMenuShareAppMessage(shareData);
wx.onMenuShareTimeline(shareData);
});
});
},
handler(val){
console.log('init',val);
this.showTabItem = val;
},
loadDoctor(){
let vm = this;
let id = tools.getRequestParam('md');
if(!id){return false;}
let data = {showCount:100,currentPage:1,signOrgId:vm.id,signType:1};
this.$http.axios({method:'post',url:'/jyDoctorInfo/getJySignDoctorInfo',data}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.items){
let tempArr = [];
for(let i=0;i<data.items.length;i++){
let item = data.items[i];
tempArr.push({src:item.imageUrl,title:item.name,desc:item.skills,url:'/m/doctorIndex.html?md='+item.managerId})
}
vm.doctorArr = tempArr;
console.log(vm.doctorArr);
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
});
}
}
}



